/* Variables */
:root {
  --main-bg-color: #333;
  --text-color: white;
  --nav-bg-color: #444;
  --hover-bg-color: #ddd;
  --hover-text-color: black;
  --main-content-bg: #f1f1f1;
  --main-color: #007bff;
}

.no-select, img {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */    /* Konqueror */      /* Firefox */       /* Internet Explorer/Edge */
  user-select: none;           /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}

div:where(.swal2-container) button:where(.swal2-styled).swal2-confirm {
  background-color: #007bff!important;
  background-color: var(--main-color)!important;
}

#swal2-html-container {
  text-align: center !important;
}

.swal2-actions {
  margin-top: 12px!important;
  justify-content: center !important;
  align-items: center !important;
}

body {
  font-family: system-ui ,'Lato', sans-serif, 'Courier New', 'Courier';
  margin: 0;
  padding: 0;
}

a { 
  color: #007bff; 
  color: var(--main-color);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  color: #0056b3;
}

/* Estilo para móvil */
/* Estilos para modo móvil */

#contactNameText {
  margin-left: 8px;
  letter-spacing: 4px;
  font-size: 25px;
  font-weight: bold;
}

/* Estilos generales */


.center-text {
text-align: center;
}

.padding-1em {
padding: 1em;
}

/* Estilos para la cabecera y el pie de página */
.header, .footer {
background-color: #333;
background-color: var(--main-bg-color);
color: white;
color: var(--text-color);
text-align: center;
padding: 1em;
}

/* Estilos para el menú de navegación */
.nav {
background-color: #444;
background-color: var(--nav-bg-color);
overflow: hidden;
}

.nav a {
float: left;
display: block;
color: white;
color: var(--text-color);
text-align: center;
padding: 14px 16px;
text-decoration: none;
}

.nav a:hover {
background-color: #ddd;
background-color: var(--hover-bg-color);
color: black;
color: var(--hover-text-color);
}

/* Estilos para el contenido principal */
.main {
margin: 15px;
padding: 15px;
background-color: #f1f1f1;
background-color: var(--main-content-bg);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button { 
    background-color: var(--main-color);
    color: var(--text-color);
    padding: 8px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

button:hover {
    background-color: #0056b3;
}
.register-container {
    max-width: 300px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 20px;
    background-color: #fff;
}

.register-container h2 {
    text-align: center;
}

#register-form {
    display: flex;
    flex-direction: column;
}

#register-form label {
    margin: 10px 0;
}

#register-form input {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.login-link {
